import { Link } from 'react-router-dom'

const Menuer = () => {
	const listData = [{
		title: '实列群',
		children: [{
			title: '用户列表1',
			href: '/ExampleList1',
		},{
			title: '用户列表2',
			href: '/ExampleList2',
		},{
			title: '用户列表3',
			href: '/ExampleList3',
		}],
	},{
		title: 'react 学习笔记 A',
		children: [{
			title: 'hooks 生命周期',
			href: '/NoteA/NoteA01',
		},{
			title: 'hooks 子父通信',
			href: '/NoteA/NoteA02',
		},{
			title: 'hooks 渲染优化1',
			href: '/NoteA/NoteA03',
		}],
	}];
	return (
		<ul className="ygbh-menu">
			{listData.map((item1) => {
				return (<li className="group-item" key={item1.title}>
					<div className="group-item-title">{item1.title}</div>
					<ul className="group-hash-list">
						{item1.children.map((item2) => {
							return (<li className="group-hash-item" key={item2.title}>
								<Link to={item2.href}>{item2.title}</Link>
							</li>);
						})}
					</ul>
				</li>);
			})}
		</ul>
	);
};

export default Menuer;
