import React from "react";
import { Redirect, Route } from "react-router-dom";
import { connect } from "react-redux";

export default connect(
    ({ loginUser }) => ({
        isLogin: loginUser.isLogin
    })
)(function PrivateRoute ({ children, isLogin, component: Comp, ...rest }) {
    console.log("路由监听组件 PrivateRoute:", isLogin, rest)
    return (
        <Route
            {...rest}
            render={({ location }) => {
                return isLogin ? (
                    <Comp />
                ) : (
                    <Redirect
                        to={{ pathname: "/Hello5", state: { redirect: location.pathname } }}
                    />
                )
            }}
        />
    );
});
