import React, { Suspense, lazy } from 'react';
import { HashRouter, Switch, Route } from 'react-router-dom';
//import createBrowserHistory from 'history/createBrowserHistory';

//路由的全局控制
import PrivateRoute from "./PrivateRoute";

import Home from "../pages/Home.jsx";

// 使用路由懒加载
const ExampleList1 = lazy(() => import( /*webpackChunkName: "router-ExampleList1"*/  '../pages/ExampleList1/index.jsx') );
const ExampleList2 = lazy(() => import( /*webpackChunkName: "router-ExampleList2"*/  '../pages/ExampleList2/index.jsx') );
const ExampleList3 = lazy(() => import( /*webpackChunkName: "router-ExampleList3"*/  '../pages/ExampleList3/index.jsx') );
//note 学习笔记
const NoteA01 = lazy(() => import( /*webpackChunkName: "router-NoteA01"*/  '../pages/NoteA/NoteA01.jsx') );
const NoteA02 = lazy(() => import( /*webpackChunkName: "router-NoteA02"*/  '../pages/NoteA/NoteA02.jsx') );
const NoteA03 = lazy(() => import( /*webpackChunkName: "router-NoteA03"*/  '../pages/NoteA/NoteA03.jsx') );

//不需要登录权限的路由
const layoutRouter1 = [
    { path:"/", component: Home,},
];
//需要登录权限限制的路由
const layoutRouter2 = [
    { path:"/ExampleList1", component: ExampleList1,},
    { path:"/ExampleList2", component: ExampleList2,},
    { path:"/ExampleList3", component: ExampleList3,},
];
const otherRouter = [
    { path:"/NoteA/NoteA01", component: NoteA01,},
    { path:"/NoteA/NoteA02", component: NoteA02,},
    { path:"/NoteA/NoteA03", component: NoteA03,},
];


function RouteConfig (props) {
    return(
        <div>
            <HashRouter>
                <Suspense fallback={<div>Loading...</div>}>
                    <Switch>
                        { layoutRouter1.map(item => <Route exact path={item.path}  component={item.component} key={item.path}/>) }
                        { layoutRouter2.map(item => <PrivateRoute exact path={item.path}  component={item.component} key={item.path}/>) }
                        { otherRouter.map(item => <Route exact path={item.path}  component={item.component} key={item.path}/>) }
                    </Switch>
                </Suspense>
            </HashRouter>
        </div>
    )
};

export default RouteConfig